<template lang="html">
    <svg class="svgicon" aria-hidden="true">
        <use :xlink:href="iconId"></use>
    </svg>
</template>

<script>
export default {
    name: 'icon',
    props: ['id'],
    computed: {
        'iconId'() {
            return `#${this.id}`;
        }
    }
};
</script>



